import React, { Component } from 'react'
import Body from './components/Body'
import TabList from './components/TabList'
import './App.css'
import NavBar from 'react-bootstrap/Navbar'


export default class App extends Component {
    constructor() {
        super();
        this.state = {
            activeTab: 1,
        }
        this.changeTab = (id) => {
            this.setState({
                activeTab: id
            })
        }
    }


    render() {
        const tabs = [
            {
              id: 1,
              title: "Home"
            }, {
              id: 2, 
              title: "Zen and Society"
            }, {
              id: 3, 
              title: "Resources"
            }
          ]
        let applyStyle
        if (this.state.activeTab == 1) {
            // applyStyle = {backgroundImage: "url(" + "https://mdbootstrap.com/img/Photos/Horizontal/Nature/full page/img(11).jpg" + ")"}
            applyStyle = {backgroundImage: `url(require("https://mdbootstrap.com/img/Photos/Horizontal/Nature/full page/img(11).jpg"))`}
        } else {
            // applyStyle = { backgroundImage: 'url()',
            //     backgroundColor: "gray"}
        }
        return (
            <div className = "outer">
            <div className = "body" style = {applyStyle}>
                <div className = "nav-bar">
                    <TabList tabs = {tabs}
                    changeTab = {this.changeTab}
                    activeTab = {this.state.activeTab}/>
                </div>
                <div className = "main-body">
                    <Body activeTab = {this.state.activeTab}/>
                </div> 
            </div>
            </div> 
        );
    }
}

